// media.scss
@media screen and (min-width: 666px) and (max-width: 800px) {
    .product-section{
		padding: 0;
        width:95%;
        margin: 6px auto 20px;
    }
    .add-cart__btn {
        padding: 7px 21px;
        font-size: 17px;
    }
    .layout-description__add-cart {
        margin-top: -35px;
    }
    .add-cart {
        font-size: 24px;
    }
    .layout-description__lineSocial {
        width: 220px;
        margin: auto;
    }
    .tab-content{
        &__layoutCategories, &__layoutitems, &__layoutadd{flex-basis: auto;}
    }	
	
    .orderFormLayout{
        width:393px;
        margin:auto;
    }
    .orderForm {
         margin-top: 70px;
        margin-bottom: 80px;
    }
    .Categories:hover {
    -webkit-animation: none;
    border: 1px solid #000;
    font-size: 16px;
    }
    .min-img{
        display: none;
    }
}

